import React, { Component, useEffect, useState } from "react";
import axios from 'axios';
import { NavLink} from "react-router-dom";
import Modal from 'react-modal';
import "../index.css";



export default function AdminHome( {userData}) {

  const [signInTime, setSignInTime] = useState(null);
  const [signOutTime, setSignOutTime] = useState(null);
  const user = userData.fname;
  const email = userData.email;
  const [searchValue, setSearchValue] = useState('');
  const [showResults, setShowResults] = useState(false);
  const [attendance, setAttendance] = useState([]);
  const [presentUsersCount, setPresentUsersCount] = useState(null);
  const [userCount, setUserCount] = useState(null);
  const [attendanceData, setAttendanceData] = useState([]);
const [date, setDate] = useState(new Date().toLocaleDateString());
const [loading, setLoading] = useState(false);

async function handleDownloadClick() {
  setLoading(true);
  try {
    const response = await fetch('http://192.11.18.54:8090/attendance-count-by-month-all');
    const blob = await response.blob();
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'attendance.csv';
    a.click();
  } catch (error) {
    console.error(error);
  } finally {
    setLoading(false);
  }
}



const handleSignIn = async () => {
  try {
    const response = await axios.post("http://192.11.18.54:8090/attendance-signin", {
      user: userData.fname,
      email: userData.email,
    });
    if (response.status === 201) {
      setSignInTime(new Date().toISOString());
    }

  } catch (error) {
    alert("You have already signed in today");

    console.error(error);
  }
};


const handleSignOut = async () => {
  try {
    const response = await axios.post("http://192.11.18.54:8090/attendance-signout", {
      user: userData.fname,
      email: userData.email,
    });
    if (response.status === 201) {
      setSignOutTime(new Date().toISOString());
    }
  
  } catch (error) {
    alert("Haven't signed in yet, or already signed out")
    console.error(error);
  }
};

    const logOut = () => {
      window.localStorage.clear();
      window.location.href = "./sign-in";
    };

    function ExternalLink() {
      const handleLinkClick = (url) => {
        window.location.href = url;
      };
    }
    const handleSearch = async (event) => {
      event.preventDefault();
    
      if (!searchValue || typeof searchValue !== 'string') {
        throw new Error('Invalid user parameter');
      }
    
      try {
        const response = await axios.get('http://192.11.18.54:8090/attendance-count-by-month', {
          params: { user: searchValue },
        });
    
        if (response.data.length === 0) {
          setUserCount(null);
          setShowResults(true);
        } else {
          setUserCount(response.data);
          setShowResults(true);
        }
    
      } catch (err) {
        console.log(err);
        throw new Error('Failed to fetch attendance data');
      }
    };
    
    
  

  useEffect(() => {
    async function fetchAttendance() {
      const response = await axios.get('http://192.11.18.54:8090/attendance-counts');
      setAttendanceData(response.data);
    }
    fetchAttendance();
  }, [attendanceData]);


  useEffect(() => {
    async function fetchCurrentPeople() {
      const response = await axios.get('http://192.11.18.54:8090/attendance-today');
      setPresentUsersCount(response.data);
    }
    fetchCurrentPeople();
  }, [presentUsersCount]);


  useEffect(() => {
    async function fetchData() {
      try {
        const res = await axios.get('http://192.11.18.54:8090/attendance');
        setAttendance(res.data);
      } catch (err) {
        console.error(err);
      }
    }
    fetchData();
  }, [attendance]);

  
 
  return (
<>

        <div className="sidebar">
    <div className="logo-details">
      <i className='bx bxl-c-plus-plus'></i>
      <span className="logo_name">{user}</span>
    </div>
      <ul className="nav-links">
        <li>
        <NavLink exact to="/" activeClassName="active">
            <i className='bx bx-grid-alt' ></i>
            <span className="links_name">Dashboard</span>
       </NavLink>
        </li>
        <li>
        <a href="https://join.slack.com/t/newworkspace-0di2154/shared_invite/zt-1qrrgq7s5-IvL_zgVs_ZvwxZbBMtnQaA" target="_blank" rel="noopener noreferrer">

            <i className='bx bx-box' ></i>
            <span className="links_name">Forum</span>
          </a>
        </li>
        <li>
          
          <NavLink to="/help" activeClassName="active">
          <i className='bx bx-box' ></i>
            <span className="links_name">Help</span>
         </NavLink>
        </li>
       
      </ul>
  </div>
  <section className="home-section">
    <nav>
      <div className="sidebar-button">
        <i className='bx bx-menu sidebarBtn'></i>
        <span className="dashboard">Dashboard</span>
      </div>
      <div>
      <div>
      <form onSubmit={handleSearch}>
      <input type="text" placeholder="Search user" onChange={(e) => setSearchValue(e.target.value)} />
      <button type="submit">Search</button>
    </form>

    
      </div>
      
      </div>
      <button onClick={logOut} className="btn btn-primary">
          Log Out
        </button>
    </nav>

    <div className="home-content">
      <div className="overview-boxes">
        <div className="box">
          <div className="right-side">
            <div className="box-topic">
            {presentUsersCount !== null ? (
  <p>At Work Currently: {presentUsersCount.count}</p>
) : (
  <p>Loading...</p>
)}

   </div>
            <div className="indicator">
            </div>
          </div>
          <i className='bx bx-cart-alt cart four'></i>
        </div>
        <div className="box">
          <div className="right-side">
            <div className="box-topic">Click To</div>
            <button onClick={handleSignIn} style={{ backgroundColor: 'lightblue', color: 'white', border: 'none', padding: '10px 20px', borderRadius: '5px' }}>SIGN IN</button>
          {signInTime && <p>Signed in at: {signInTime}</p>}

          </div>
          <i className='bx bxs-cart-add cart five' ></i>
        </div>
        <div className="box">
          <div className="right-side">
            <div className="box-topic">Click To</div>
            <button onClick={handleSignOut} style={{ backgroundColor: 'lightblue', color: 'white', border: 'none', padding: '10px 20px', borderRadius: '5px' }}>SIGN OUT</button>
            {signOutTime && <p>Signed out at: {signOutTime}</p>}
          </div>
          
          <i className='bx bx-cart cart five' ></i>
        </div>
        <div className="box">
          <div className="right-side">
            <div className="box-topic">Export History</div>
            <button onClick={handleDownloadClick} disabled={loading} style={{ backgroundColor: 'lightblue', color: 'white', border: 'none', padding: '10px 20px', borderRadius: '5px' }}>EXPORT</button>
            {loading ? 'Downloading...' : 'Attendance Report'}
          </div>
          <i className='bx bxs-cart-download cart five' ></i>
        </div>
      </div>

      <div className="sales-boxes">
      <div className="recent-sales box">
  <div className="title">Attendance for {date}</div>
  <div style={{ display: 'flex', justifyContent: 'center' }}>
    <table className="styled-table">
      <thead style={{ backgroundColor: 'lightblue', color: 'white' }}>
        <tr>
          <th>Name</th>
          <th>Email</th>
          <th>Sign in</th>
          <th>Sign out</th>
        </tr>
      </thead>
      <tbody>
        {attendance.map((entry, index) => (
          <tr key={index}>
            <td>{entry.name}</td>
            <td>{entry.email}</td>
            <td>{entry.signIn}</td>
            <td>{entry.signOut}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </div>
</div>

        <div  className="top-sales box">
          <div className="title">Attendance this month</div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
          {attendanceData ? (
              <table className="styled-tables">
              <thead style={{ backgroundColor: 'lightblue', color: 'white' }}>
                <tr>
                  <th>User</th>
                  <th>Count</th>
                </tr>
              </thead>
              <tbody>
                {attendanceData.map((item) => (
                  <tr key={item.user}>
                    <td>{item.user}</td>
                    <td>{item.count}</td>
                  </tr>
                ))}
              </tbody>
            </table>) : (
              <p>Loading attendance data...</p>
            )}
            </div>
        </div>
      </div>
      <div className="show">
      {showResults && (
        <div>
          <p> {userCount.user}</p>
          <ul>
            {Object.entries(userCount).map(([month, count]) => (
              <li key={month}>{month}: {count}</li>
            ))}
          </ul>
        </div>
      )}
      </div>
    </div>
  </section>
  
        </>
  );
}
