import React, { useState } from "react";
import "../Button.css";
import axios from "axios";
import { Link } from "react-router-dom";

export default function UserHome({ userData }) {
  const [signInTime, setSignInTime] = useState(null);
  const [signOutTime, setSignOutTime] = useState(null);

  const handleSignIn = async () => {
    try {
      const response = await axios.post("http://192.11.18.54:8090/attendance-signin", {
        user: userData.fname,
        email: userData.email,
      });
      if (response.status === 201) {
        setSignInTime(new Date().toISOString());
      }
  
    } catch (error) {
      alert("You have already signed in today");

      console.error(error);
    }
  };

  const handleSignOut = async () => {
    try {
      const response = await axios.post("http://192.11.18.54:8090/attendance-signout", {
        user: userData.fname,
        email: userData.email,
      });
      if (response.status === 201) {
        setSignOutTime(new Date().toISOString());
      }
    
    } catch (error) {
      alert("Haven't signed in yet, or already signed out")
      console.error(error);
    }
  };

  const logOut = () => {
    window.location.href = "./sign-in";
  };

  return (
    <div className="home-page">
      <header className="header">
        <nav>
          <ul>
            <li> <a href="https://join.slack.com/t/newworkspace-0di2154/shared_invite/zt-1qrrgq7s5-IvL_zgVs_ZvwxZbBMtnQaA" target="_blank" rel="noopener noreferrer">Forum</a></li>
            <li><Link to="/help">Help</Link></li>
          </ul>
        </nav>
      </header>
      <div className="auth-wrapper">
        <div className="content"> 
          <div className="auth-inner">
            <div>
              WELCOME !<h1>{userData.fname}</h1>
              TAKE ATTENDANCE :{" "}
              <div>
                <button onClick={handleSignIn} style={{ backgroundColor: 'lightgreen', color: 'gray', border: 'none', padding: '10px 20px', borderRadius: '5px' }}>SIGN IN</button>
                <button onClick={handleSignOut} style={{ backgroundColor: 'pink', color: 'gray', border: 'none', padding: '10px 20px', borderRadius: '5px' }}>SIGN OUT</button>
              </div>
              {signInTime && <p>Signed in at: {signInTime}</p>}
              {signOutTime && <p>Signed out at: {signOutTime}</p>}
              <br />
              <button onClick={logOut} className="btn btn-primary">
                Log Out
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
